<template>
    <div class='coachlist'>
        <div class='title'>教练管理</div>
        <Table  :tableData="tableData" :tableQuery="tableQuery"  :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" :currentPage="currentPage"  v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" :operateBtn="operateBtn"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {coachlist, unbundCoach} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  name: 'coachlist',
  data () {
    return {
      tableQuery: [
        {
          name: 'name',
          title: '输入教练姓名',
          type: 'input',
          model: '',
          text: '关键词：'
        },
        {
          name: 'accountMobile',
          title: '输入教练手机号码',
          type: 'input',
          model: '',
          text: '联系方式：'
        },
        // {
        //   name: 'certificatedStatus',
        //   type: 'select',
        //   model: '',
        //   title: '请选择审核状态',
        //   text: '审核状态：',
        //   selectArr: [],
        //   style: {width: '73%'}
        // },
        {
          name: 'START_DATE',
          type: 'start_date',
          model: '',
          title: '选择开始日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '入职时间：'
        },
        {
          name: 'END_DATE',
          type: 'end_date',
          model: '',
          title: '选择结束日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '到'
        }
      ],
      tableKey: [
        {name: 'ID', value: 'coach.personId', nor: true},
        {name: '姓名', value: 'coach.personName', nor: true},
        {name: '性别', value: 'coach.personSex', nor: true},
        {name: '联系方式', value: 'coach.personPhone', nor: true},
        {name: '证件类型', value: 'coach.personalType', nor: true},
        {name: '证件号', value: 'coach.personalIdNo', nor: true},
        {name: '出生日期', value: 'coach.personBirthday', nor: true},
        {name: '入职时间', value: 'coach.hireDate', nor: true},
        {name: '学员数量', value: 'coach.studentList', hov1: true}
      ],
      tableData: [
      ],
      isPage: true,
      total: 0,
      pageSize: 10,
      currentPage: 1,
      para: {page: 1, pageNumber: 10, name: '', accountMobile: '', startHireDate: '', endHireDate: ''},
      operateBtn: [{text: '详情'}, {text: '解除关系', style:'color: #fc7563;'}]
    }
  },
  components: {
    Table
  },
  created () {
    // 获取教练信息
    this.$nextTick(_ => {
      // this.listSelect()
      this.initCoach(this.para)
    })
  },
  methods: {
    // 下拉框赋值
    // listSelect () {
    //   let option1 = [
    //     {
    //       value: '',
    //       label: '全部'
    //     },
    //     {
    //       value: 'WaitingCertificated',
    //       label: '待审核'
    //     },
    //     {
    //       value: 'NoCertificated',
    //       label: '未完成'
    //     },
    //     {
    //       value: 'CertificateSuccessed',
    //       label: '已通过'
    //     },
    //     {
    //       value: 'CertificateFailed',
    //       label: '已驳回'
    //     }
    //   ]
    //   this.$refs.Table.reloadValue('certificatedStatus', option1)
    // },
    getQueryParam (queryObj) {
      console.log('获取参数', queryObj)
      // this.currentList = []
      // this.IndexLsit = []
      this.para.page = 1
      this.para.accountMobile = queryObj.accountMobile
      this.para.name = queryObj.name
      if (queryObj.START_DATE) {
        this.para.startHireDate = queryObj.START_DATE
      } else {
        this.para.startHireDate = ''
      }
      if (queryObj.END_DATE) {
        this.para.endHireDate = queryObj.END_DATE
      } else {
        this.para.endHireDate = ''
      }
      // if (queryObj.certificatedStatus) {
      //   this.para.certificatedStatus = queryObj.certificatedStatus
      // } else {
      //   delete this.para.certificatedStatus
      // }
      this.initCoach(this.para)
    },
    changeParam (data) {
      this.para.page = data
      this.initCoach(this.para)
      console.log('获取数据', data)
    },
    // 获取教练信息
    async initCoach (data) {
      const res = await coachlist(this.para)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.total = res.data.count
          this.tableData = res.data.list
        }
        if (this.total === 0) {
          this.isPage = false
        } else {
          this.isPage = true
        }
        console.log('获取教练信息', res)
      } else {
        this.isPage = false
      }
    },
    // 详情跳入
    getRowdata (data) {
      if (data.index === '详情') {
        setSessionStorage('detailInfo', data.row)
        this.$router.push({name: 'updatecoach', query: {id: data.row.coach.personId, status: data.row.coach.coachCertificatedStatus}})
      } else if (data.index === '解除关系') {
          let self = this
          this.$confirm('解除教练关系后，教练将不再获取健身房的配置信息，确认要解除么？', 
            {
              showClose: false
            })
            .then(_ => {
              self.deleteRelation(data.row.coach.personId)
            })
            .catch(_ => {})
      }
    },
    // 解除关系
    async deleteRelation (data) {
      const res = await unbundCoach (data)
      if (isApiResponseOk(res)) {
        this.$message({
          showClose: true,
          message: '解除关系成功',
          type: 'success'
        })
        this.initCoach(this.para)
      }
    }
  }
}
</script>



